import React, { useEffect, useRef, useState, useCallback } from 'react';
import useEmblaCarousel from 'embla-carousel-react';
import { ArrowLeft, ArrowRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useNavigate } from 'react-router-dom';

const TMDB_BACKDROP = 'https://image.tmdb.org/t/p/original';

export default function HeroCarousel({ items = [], autoplay = true, autoplayInterval = 5000 }) {
  // embla with one slide per view
  const [emblaRef, embla] = useEmblaCarousel({ loop: true, align: 'start', containScroll: 'trimSnaps' });
  const autoplayRef = useRef(null);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const navigate = useNavigate();

  useEffect(() => {
    if (!embla) return;
    const onSelect = () => setSelectedIndex(embla.selectedScrollSnap());
    embla.on('select', onSelect);
    onSelect();
    return () => embla.off('select', onSelect);
  }, [embla]);

  const scrollPrev = useCallback(() => embla && embla.scrollPrev(), [embla]);
  const scrollNext = useCallback(() => embla && embla.scrollNext(), [embla]);

  useEffect(() => {
    if (!embla || !autoplay) return;
    const play = () => {
      if (!embla) return;
      const next = embla.selectedScrollSnap() + 1;
      const snaps = embla.scrollSnapList();
      if (next >= snaps.length) embla.scrollTo(0);
      else embla.scrollTo(next);
    };
    autoplayRef.current = setInterval(play, autoplayInterval);
    const vp = embla && embla.containerNode();
    const pause = () => clearInterval(autoplayRef.current);
    const resume = () => {
      clearInterval(autoplayRef.current);
      autoplayRef.current = setInterval(play, autoplayInterval);
    };
    if (vp) {
      vp.addEventListener('pointerenter', pause);
      vp.addEventListener('pointerleave', resume);
    }
    return () => {
      clearInterval(autoplayRef.current);
      if (vp) {
        vp.removeEventListener('pointerenter', pause);
        vp.removeEventListener('pointerleave', resume);
      }
    };
  }, [embla, autoplay, autoplayInterval]);

  if (!items || items.length === 0) return null;

  const daysNew = 30;
  const now = new Date();

  const truncate = (str, n = 300) => {
    if (!str) return '';
    return str.length > n ? str.slice(0, n - 1) + '' : str;
  };

  return (
    <div className="w-full relative">
      <div className="embla overflow-hidden" ref={emblaRef}>
        <div className="embla__container flex">
          {items.map((item, idx) => {
            const backdrop = item.backdrop_path || item.poster_path;
            const bg = backdrop ? `${TMDB_BACKDROP}${backdrop}` : null;
            const title = item.title || item.name || item.original_title;
            const overview = item.overview || '';
            const dateSrc = item._date || item.release_date || item.first_air_date;
            const parsedDate = dateSrc ? new Date(dateSrc) : new Date('1970-01-01');
            const isNew = !isNaN(parsedDate) && ((now - parsedDate) / (1000 * 60 * 60 * 24) <= daysNew);
            const shortOverview = truncate(overview, 300);
            return (
              <div key={item.id || idx} className="embla__slide min-h-[45vh] sm:min-h-[55vh] w-full flex-shrink-0 relative">
                {bg ? (
                  <img src={bg} alt={title} className="absolute inset-0 w-full h-full object-cover" loading="lazy" decoding="async" />
                ) : (
                  <div className="absolute inset-0 bg-gradient-to-r from-slate-800 via-slate-900 to-slate-800" />
                )}
                <div className="absolute inset-0 bg-gradient-to-r from-black/70 via-transparent to-transparent" />
                <div className="relative z-10 container mx-auto py-20 px-4 flex items-center" style={{minHeight: '55vh'}}>
                  <div className="max-w-3xl text-left">
                    <div className="flex items-center gap-3 mb-3">
                      <h2 className="text-3xl sm:text-5xl font-bold text-white">{title}</h2>
                      {isNew && (
                        <span className="inline-block bg-rose-500 text-white text-xs font-semibold px-2 py-1 rounded">Nuevo</span>
                      )}
                    </div>
                    <p className="text-gray-200 mb-6 line-clamp-4">{shortOverview}</p>
                    <div className="flex gap-3">
                      <Button aria-label={`ver-detalles-${item.id}`} onClick={() => navigate(`/content/${item.id}`)} className="bg-gradient-to-r from-cyan-500 to-blue-500">Ver detalles</Button>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* navigation arrows removed as requested */}

      {/* dots */}
      <div className="absolute bottom-4 left-0 right-0 flex items-center justify-center gap-2">
        {Array.from({ length: items.length }).map((_, idx) => (
          <button key={idx} onClick={() => embla && embla.scrollTo(idx)} className={`w-3 h-3 rounded-full ${selectedIndex === idx ? 'bg-white' : 'bg-white/30'}`} aria-label={`go-to-${idx}`} />
        ))}
      </div>
    </div>
  );
}
