import React, { useCallback, useEffect, useState, useRef } from 'react';
import useEmblaCarousel from 'embla-carousel-react';
import { ArrowLeft, ArrowRight } from 'lucide-react';

// Embla-based carousel with swipe, dots and optional autoplay
export default function Carousel({ items = [], autoplay = true, autoplayInterval = 3500, showArrows = true, showDots = false }) {
  const [emblaRef, embla] = useEmblaCarousel({ loop: false, align: 'center' });
  const viewportNode = useRef(null);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const autoplayRef = useRef(null);
  const [slideWidth, setSlideWidth] = useState('33.333%');
  // allow smaller cards on large screens so we can show up to 6
  const minSlideWidth = '120px';

  const scrollTo = useCallback((index) => embla && embla.scrollTo(index), [embla]);

  useEffect(() => {
    if (!embla) return;
    const onSelect = () => setSelectedIndex(embla.selectedScrollSnap());
    embla.on('select', onSelect);
    onSelect();
    return () => embla.off('select', onSelect);
  }, [embla]);

  // responsive slide width: 2 per view for very small screens, 3 otherwise
  useEffect(() => {
    const update = () => {
      try {
        const w = window.innerWidth;
        // breakpoints control how many cards visible per view:
        // <=420px -> 2 per view (50%)
        // <=640px -> 3 per view (33.333%)
        // <=1024px -> 4 per view (25%)
        // <=1280px -> 5 per view (20%)
        // >1280px -> 6 per view (~16.6667%)
        if (w <= 420) setSlideWidth('50%');
        else if (w <= 640) setSlideWidth('33.333%');
        else if (w <= 1024) setSlideWidth('25%');
        else if (w <= 1280) setSlideWidth('20%');
        else setSlideWidth('16.6667%');
      } catch {}
    };
    update();
    window.addEventListener('resize', update);
    return () => window.removeEventListener('resize', update);
  }, []);

  // autoplay with pause on hover
  useEffect(() => {
    if (!embla || !autoplay) return;
    const play = () => {
      if (!embla) return;
      const snaps = embla.scrollSnapList();
      const next = embla.selectedScrollSnap() + 1;
      if (next >= snaps.length) {
        embla.scrollTo(0);
      } else {
        embla.scrollTo(next);
      }
    };
    autoplayRef.current = setInterval(play, autoplayInterval);

    const vp = viewportNode.current;
    const pause = () => clearInterval(autoplayRef.current);
    const resume = () => {
      clearInterval(autoplayRef.current);
      autoplayRef.current = setInterval(play, autoplayInterval);
    };
    if (vp) {
      vp.addEventListener('pointerenter', pause);
      vp.addEventListener('pointerleave', resume);
    }
    return () => {
      clearInterval(autoplayRef.current);
      if (vp) {
        vp.removeEventListener('pointerenter', pause);
        vp.removeEventListener('pointerleave', resume);
      }
    };
  }, [embla, autoplay, autoplayInterval]);

  return (
    <div className="w-full relative">
      <div className="overflow-hidden" ref={(node) => { emblaRef(node); viewportNode.current = node; }}>
        <div className="flex">
          {items.map((child, i) => (
            <div key={i} className="flex-shrink-0 px-2" style={{ width: slideWidth, minWidth: minSlideWidth }}>
              {child}
            </div>
          ))}
        </div>
      </div>

      {showArrows && (
        <>
          <button
            aria-label="anterior"
            onClick={() => embla && embla.scrollPrev()}
            className="absolute left-2 top-1/2 -translate-y-1/2 bg-white/10 text-white rounded-full p-2 hidden sm:inline"
          >
            <ArrowLeft className="w-4 h-4" />
          </button>
          <button
            aria-label="siguiente"
            onClick={() => embla && embla.scrollNext()}
            className="absolute right-2 top-1/2 -translate-y-1/2 bg-white/10 text-white rounded-full p-2 hidden sm:inline"
          >
            <ArrowRight className="w-4 h-4" />
          </button>
        </>
      )}

      {/* dots (optional) */}
      {showDots && (
        <div className="flex items-center justify-center gap-2 mt-3">
          {(embla ? embla.scrollSnapList() : Array.from({ length: items.length })).map((_, idx) => (
            <button
              key={idx}
              onClick={() => scrollTo(idx)}
              className={`w-2 h-2 rounded-full ${selectedIndex === idx ? 'bg-white' : 'bg-white/30'}`}
              aria-label={`go-to-slide-${idx}`}
            />
          ))}
        </div>
      )}
    </div>
  );
}
