import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { AuthContext } from "@/App";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { toast } from "sonner";
import { ArrowLeft, User, Lock, Bookmark, MessageSquare, Star, Trash2 } from "lucide-react";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;
const TMDB_IMAGE_BASE = "https://image.tmdb.org/t/p/w500";

const UserProfile = () => {
  const { user } = useContext(AuthContext);
  const navigate = useNavigate();
  const [watchlist, setWatchlist] = useState([]);
  const [userReviews, setUserReviews] = useState([]);
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  useEffect(() => {
    if (!user) {
      navigate("/");
      return;
    }
    fetchWatchlist();
    fetchUserReviews();
  }, [user]);

  const fetchWatchlist = async () => {
    try {
      const response = await axios.get(`${API}/watchlist`);
      setWatchlist(response.data);
    } catch (error) {
      console.error("Error fetching watchlist", error);
    }
  };

  const fetchUserReviews = async () => {
    try {
      const response = await axios.get(`${API}/user/reviews`);
      setUserReviews(response.data);
    } catch (error) {
      console.error("Error fetching user reviews", error);
    }
  };

  const handleChangePassword = async () => {
    if (newPassword !== confirmPassword) {
      toast.error("Las contraseas no coinciden");
      return;
    }

    if (newPassword.length < 6) {
      toast.error("La contrasea debe tener al menos 6 caracteres");
      return;
    }

    try {
      await axios.post(`${API}/user/change-password`, {
        old_password: oldPassword,
        new_password: newPassword,
      });
      toast.success("Contrasea cambiada exitosamente");
      setOldPassword("");
      setNewPassword("");
      setConfirmPassword("");
    } catch (error) {
      toast.error(error.response?.data?.detail || "Error al cambiar contrasea");
    }
  };

  const handleRemoveFromWatchlist = async (contentId) => {
    try {
      await axios.delete(`${API}/watchlist/${contentId}`);
      toast.success("Eliminado de la lista");
      fetchWatchlist();
    } catch (error) {
      toast.error("Error al eliminar de la lista");
    }
  };

  const handleDeleteReview = async (reviewId) => {
    try {
      await axios.delete(`${API}/reviews/${reviewId}`);
      toast.success("Resea eliminada");
      fetchUserReviews();
    } catch (error) {
      toast.error("Error al eliminar resea");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900 px-4 py-8 sm:p-8">
      <div className="container mx-auto">
  <div className="flex flex-row items-center justify-between mb-8 gap-4 transition-all duration-200 ease-in-out flex-wrap">
          <h1 className="hidden sm:block text-3xl sm:text-4xl font-bold bg-gradient-to-r from-cyan-400 to-blue-400 bg-clip-text text-transparent truncate max-w-xs">
            Mi Perfil
          </h1>
          <Button
            data-testid="back-to-home-button"
            onClick={() => navigate("/")}
            variant="outline"
            className="flex items-center justify-center border-white/20 text-white hover:bg-white/10 px-2 py-2 rounded"
          >
            <ArrowLeft className="w-4 h-4 sm:mr-2 mr-0" />
            <span className="hidden sm:inline truncate">Volver al Inicio</span>
          </Button>
        </div>

        <Tabs defaultValue="info" className="space-y-6">
          <TabsList className="bg-white/5 border border-white/10 flex gap-2 overflow-x-auto no-scrollbar py-1 px-1">
            <TabsTrigger value="info" data-testid="info-tab" className="flex items-center gap-2 min-w-0">
              <User className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Informacin</span>
            </TabsTrigger>
            <TabsTrigger value="watchlist" data-testid="watchlist-tab" className="flex items-center gap-2 min-w-0">
              <Bookmark className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Ver Luego ({watchlist.length})</span>
            </TabsTrigger>
            <TabsTrigger value="reviews" data-testid="reviews-tab" className="flex items-center gap-2 min-w-0">
              <MessageSquare className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Mis Reseas ({userReviews.length})</span>
            </TabsTrigger>
            <TabsTrigger value="password" data-testid="password-tab" className="flex items-center gap-2 min-w-0">
              <Lock className="w-4 h-4" />
              <span className="hidden sm:inline truncate">Cambiar Contrasea</span>
            </TabsTrigger>
          </TabsList>

          {/* User Info Tab */}
          <TabsContent value="info">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Informacin del Usuario</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center space-x-4 min-w-0">
                  <div className="w-12 h-12 sm:w-16 sm:h-16 rounded-full bg-gradient-to-r from-cyan-500 to-blue-500 flex items-center justify-center flex-shrink-0">
                    <User className="w-7 h-7 sm:w-8 sm:h-8 text-white" />
                  </div>
                  <div className="min-w-0">
                    <p className="hidden sm:block text-xl font-semibold text-white truncate">{user?.email}</p>
                    <p className="hidden sm:block text-gray-400">
                      Rol: <span className="text-cyan-400">{user?.role === "admin" ? "Administrador" : "Usuario"}</span>
                    </p>
                  </div>
                </div>
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 mt-4">
                  <div className="glass rounded-xl p-3 text-center">
                    <p className="text-2xl sm:text-3xl font-bold text-cyan-400">{watchlist.length}</p>
                    <p className="text-xs sm:text-sm text-gray-400 mt-1">Para ver luego</p>
                  </div>
                  <div className="glass rounded-xl p-3 text-center">
                    <p className="text-2xl sm:text-3xl font-bold text-blue-400">{userReviews.length}</p>
                    <p className="text-xs sm:text-sm text-gray-400 mt-1">Reseas</p>
                  </div>
                  <div className="glass rounded-xl p-3 text-center">
                    <p className="text-2xl sm:text-3xl font-bold text-purple-400">
                      {userReviews.length > 0 ? (userReviews.reduce((acc, r) => acc + r.rating, 0) / userReviews.length).toFixed(1) : "0"}
                    </p>
                    <p className="text-xs sm:text-sm text-gray-400 mt-1">Calificacin promedio</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Watchlist Tab */}
          <TabsContent value="watchlist">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Contenido para Ver Luego</CardTitle>
              </CardHeader>
              <CardContent>
                {watchlist.length === 0 ? (
                  <p className="text-gray-400 text-center py-8">No tienes contenido guardado</p>
                ) : (
                  <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
                    {watchlist.map((content) => (
                      <div key={content.id} className="glass rounded-xl overflow-hidden group relative">
                        <div
                          className="cursor-pointer"
                          onClick={() => navigate(`/content/${content.id}`)}
                        >
                          <div className="relative aspect-[2/3] overflow-hidden">
                            <img
                              src={
                                content.poster_path
                                  ? `${TMDB_IMAGE_BASE}${content.poster_path}`
                                  : "https://via.placeholder.com/500x750?text=No+Image"
                              }
                              alt={content.title}
                              className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500"
                            />
                          </div>
                          <div className="p-3">
                            <h4 className="font-semibold text-white truncate text-sm max-w-full">{content.title}</h4>
                          </div>
                        </div>
                        <Button
                          data-testid={`remove-watchlist-${content.id}`}
                          onClick={(e) => {
                            e.stopPropagation();
                            handleRemoveFromWatchlist(content.id);
                          }}
                          variant="ghost"
                          size="sm"
                          className="absolute top-2 right-2 text-red-400 hover:text-red-300 hover:bg-red-500/20 backdrop-blur-sm"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Reviews Tab */}
          <TabsContent value="reviews">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Mis Reseas</CardTitle>
              </CardHeader>
              <CardContent>
                {userReviews.length === 0 ? (
                  <p className="text-gray-400 text-center py-8">No has escrito reseas todava</p>
                ) : (
                  <div className="space-y-3">
                    {userReviews.map((review) => (
                      <div key={review.id} className="flex flex-col sm:flex-row items-start sm:items-start justify-between p-4 bg-white/5 rounded-lg border border-white/10">
                              <div className="flex-1 mb-3 sm:mb-0">
                          <div className="flex gap-1 mb-2">
                            {[...Array(5)].map((_, idx) => (
                              <Star
                                key={idx}
                                className={`w-4 h-4 ${
                                  idx < review.rating
                                    ? "fill-yellow-400 text-yellow-400"
                                    : "text-gray-600"
                                }`}
                              />
                            ))}
                          </div>
                          <p className="text-gray-300">{review.review_text}</p>
                          <p className="text-xs text-gray-500 mt-2">
                            {new Date(review.created_at).toLocaleDateString()}
                          </p>
                        </div>
                              <div>
                                <Button
                                  data-testid={`delete-user-review-${review.id}`}
                                  onClick={() => handleDeleteReview(review.id)}
                                  variant="ghost"
                                  size="sm"
                                  className="text-red-400 hover:text-red-300 hover:bg-red-500/10"
                                >
                                  <Trash2 className="w-4 h-4" />
                                  <span className="hidden sm:inline ml-2">Eliminar</span>
                                </Button>
                              </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Change Password Tab */}
          <TabsContent value="password">
            <Card className="bg-slate-800/50 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">Cambiar Contrasea</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <label className="text-sm text-gray-400 mb-2 block">Contrasea Actual</label>
                  <Input
                    data-testid="old-password-input"
                    type="password"
                    placeholder="Contrasea actual"
                    value={oldPassword}
                    onChange={(e) => setOldPassword(e.target.value)}
                    className="bg-slate-900/50 border-white/10 text-white"
                  />
                </div>
                <div>
                  <label className="text-sm text-gray-400 mb-2 block">Nueva Contrasea</label>
                  <Input
                    data-testid="new-password-input"
                    type="password"
                    placeholder="Nueva contrasea"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    className="bg-slate-900/50 border-white/10 text-white"
                  />
                </div>
                <div>
                  <label className="text-sm text-gray-400 mb-2 block">Confirmar Nueva Contrasea</label>
                  <Input
                    data-testid="confirm-password-input"
                    type="password"
                    placeholder="Confirmar contrasea"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    className="bg-slate-900/50 border-white/10 text-white"
                  />
                </div>
                <Button
                  data-testid="change-password-button"
                  onClick={handleChangePassword}
                  className="w-full bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600"
                >
                  Cambiar Contrasea
                </Button>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
};

export default UserProfile;
