import React, { useEffect, useState } from 'react';
import axios from 'axios';
import ContentCard from '@/components/ui/content-card';
import { useNavigate, useSearchParams } from 'react-router-dom';
import FilterBar from '@/components/FilterBar';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8000';
const API = `${BACKEND_URL}/api`;

const SeriesPage = () => {
  const [items, setItems] = useState([]);
  const [visibleCount, setVisibleCount] = useState(12);
  const [selectedGenre, setSelectedGenre] = useState(undefined);
  const [selectedCountry, setSelectedCountry] = useState(undefined);
  const [selectedYear, setSelectedYear] = useState(undefined);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();

  useEffect(() => {
    const STORAGE_KEY = 'showlib_series';

    const fetchData = async () => {
      // try load cache first
      try {
        const raw = localStorage.getItem(STORAGE_KEY);
        if (raw) {
          const parsed = JSON.parse(raw);
          if (parsed && Array.isArray(parsed.data)) {
            setItems(parsed.data);
          }
        }
      } catch {}

      try {
        const resp = await axios.get(`${API}/content`);
        const series = resp.data.filter((c) => c.content_type === 'tv');

        let shouldUpdate = true;
        try {
          const raw = localStorage.getItem(STORAGE_KEY);
          if (raw) {
            const parsed = JSON.parse(raw);
            const oldIds = Array.isArray(parsed.ids) ? parsed.ids : [];
            const newIds = series.map((s) => s.id);
            if (JSON.stringify(oldIds) === JSON.stringify(newIds)) {
              shouldUpdate = false;
            }
          }
        } catch {}

        if (shouldUpdate) {
          setItems(series);
          try { localStorage.setItem(STORAGE_KEY, JSON.stringify({ data: series, ids: series.map((s) => s.id), updatedAt: Date.now() })); } catch {}
        }
      } catch (e) {
        console.error('Error fetching series', e);
        try {
          const cached = localStorage.getItem(STORAGE_KEY);
          if (cached) {
            const parsed = JSON.parse(cached);
            setItems(parsed.data || []);
            setError('Usando datos cacheados (offline)');
          } else {
            setError('No hay datos disponibles y el backend no responde');
          }
        } catch (err) {
          setError('Error leyendo cache local');
        }
      } finally { setLoading(false); }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const g = searchParams.get('genre');
    const c = searchParams.get('country');
    const y = searchParams.get('year');
    setSelectedGenre(g ?? undefined);
    setSelectedCountry(c ?? undefined);
    setSelectedYear(y ?? undefined);
  }, [searchParams]);

  useEffect(() => {
    const params = new URLSearchParams(searchParams);
    if (selectedGenre) params.set('genre', selectedGenre);
    else params.delete('genre');
    if (selectedCountry) params.set('country', selectedCountry);
    else params.delete('country');
    if (selectedYear) params.set('year', selectedYear);
    else params.delete('year');
    setSearchParams(params, { replace: true });
  }, [selectedGenre, selectedCountry, selectedYear, searchParams, setSearchParams]);

  return (
    <div className="min-h-screen pt-24 container mx-auto px-4">
      <h2 className="text-3xl font-bold mb-6 text-blue-400">Todas las Series</h2>

      <FilterBar
        genres={Array.from(new Set(items.flatMap((i) => i.genres || [])))}
        countries={Array.from(new Set(items.flatMap((i) => i.countries || [])))}
        years={Array.from(new Set(items.map((i) => (i.release_date || '').slice(0,4)).filter(Boolean)))}
        selectedGenre={selectedGenre}
        setSelectedGenre={setSelectedGenre}
        selectedCountry={selectedCountry}
        setSelectedCountry={setSelectedCountry}
        selectedYear={selectedYear}
        setSelectedYear={setSelectedYear}
      />

      {loading && <p className="text-gray-300">Cargando...</p>}
      {error && <p className="text-yellow-300">{String(error)}</p>}

      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {items
          .filter((item) => {
            if (selectedGenre && !(item.genres || []).includes(selectedGenre)) return false;
            if (selectedCountry && !(item.countries || []).includes(selectedCountry)) return false;
            if (selectedYear) {
              const y = (item.release_date || item.first_air_date || '').slice(0,4);
              if (!y || y !== selectedYear) return false;
            }
            return true;
          })
          .slice(0, visibleCount)
          .map((item) => (
            <ContentCard key={item.id} content={item} onClick={() => navigate(`/content/${item.id}`)} />
          ))}
      </div>
      {visibleCount < items.length && (
        <div className="flex justify-center mt-6">
          <button onClick={() => setVisibleCount((v) => v + 12)} className="px-4 py-2 bg-white/5 rounded">Cargar ms</button>
        </div>
      )}
    </div>
  );
};

export default SeriesPage;
