import React, { useState, useEffect, useContext } from "react";
import axios from "axios";
import { AuthContext, SearchContext } from "@/App";
import { useNavigate } from "react-router-dom";
import Carousel from "@/components/ui/carousel";
import ContentCard from "@/components/ui/content-card";
import HeroCarousel from "@/components/ui/hero-carousel";

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;
const TMDB_IMAGE_BASE = "https://image.tmdb.org/t/p/w500";

const HomePage = () => {
  const { user, login, logout } = useContext(AuthContext);
  const { searchQuery } = useContext(SearchContext);
  const [content, setContent] = useState(() => {
    // Intenta cargar desde localStorage al iniciar
    try {
      const cached = localStorage.getItem("showlib_content");
      return cached ? JSON.parse(cached) : [];
    } catch {
      return [];
    }
  });
    
  const [filteredContent, setFilteredContent] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
  // Si hay conexin, actualiza desde el backend
  fetchContent();
  // Si no hay conexin, se mantiene el cache
  // (fetchContent ya maneja error de conexin)
  }, []);

  useEffect(() => {
    if (searchQuery) {
      const filtered = content.filter((item) =>
        item.title.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setFilteredContent(filtered);
    } else {
      setFilteredContent(content);
    }
  }, [searchQuery, content]);

  const fetchContent = async () => {
    try {
      const response = await axios.get(`${API}/content`);
      setContent(response.data);
      setFilteredContent(response.data);
      // Guarda en localStorage
      localStorage.setItem("showlib_content", JSON.stringify(response.data));
    } catch (error) {
      console.error("Error fetching content", error);
      // Si falla la conexin, usa el cache
      try {
        const cached = localStorage.getItem("showlib_content");
        if (cached) {
          setContent(JSON.parse(cached));
          setFilteredContent(JSON.parse(cached));
        }
      } catch {}
    }
  };

  // Authentication is handled by the shared Header component.

  const movies = filteredContent.filter((c) => c.content_type === "movie");
  const tvShows = filteredContent.filter((c) => c.content_type === "tv");
  const novelas = filteredContent.filter((c) => c.content_type === "novela");

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-blue-900 to-slate-900">
      {/* Header is provided by the shared Header component */}

      {/* Hero Section: latest 5 releases carousel */}
      <section className="pt-24 pb-8 px-0">
        <HeroCarousel items={
          // build list of content sorted by release/air date desc and take 5
          content
            .map((c) => ({
              ...c,
              _date: new Date((c.release_date || c.first_air_date || '1970-01-01').slice(0,10))
            }))
            .filter((c) => c._date.toString() !== 'Invalid Date')
            .sort((a,b) => b._date - a._date)
            .slice(0,5)
        } autoplay={true} autoplayInterval={5000} />
      </section>

      {/* Content Section: SIEMPRE visible para todos */}
      <section className="px-4 pb-16">
        <div className="container mx-auto">
          {/* Movies */}
          {movies.length > 0 && (
            <div className="mb-12">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-2xl sm:text-3xl font-bold text-cyan-400">Pelculas</h3>
                <div className="flex gap-2">
                  <button onClick={() => navigate('/peliculas')} className="text-sm text-white/90 bg-white/5 px-3 py-1 rounded">Ver todas</button>
                </div>
              </div>
              <div>
                <Carousel items={movies.slice(-10).map((item) => (
                  <ContentCard key={item.id} content={item} onClick={() => navigate(`/content/${item.id}`)} />
                ))} showArrows={true} autoplay={true} autoplayInterval={4000} showDots={false} />
              </div>
            </div>
          )}

          {/* TV Shows */}
          {tvShows.length > 0 && (
            <div className="mb-12">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-2xl sm:text-3xl font-bold text-blue-400">Series</h3>
                <div className="flex gap-2">
                  <button onClick={() => navigate('/series')} className="text-sm text-white/90 bg-white/5 px-3 py-1 rounded">Ver todas</button>
                </div>
              </div>
              <div>
                <Carousel items={tvShows.slice(-10).map((item) => (
                  <ContentCard key={item.id} content={item} onClick={() => navigate(`/content/${item.id}`)} />
                ))} showArrows={true} autoplay={true} autoplayInterval={4200} showDots={false} />
              </div>
            </div>
          )}

          {/* Novelas */}
          {novelas.length > 0 && (
            <div className="mb-12">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-2xl sm:text-3xl font-bold text-purple-400">Novelas</h3>
                <div className="flex gap-2">
                  <button onClick={() => navigate('/novelas')} className="text-sm text-white/90 bg-white/5 px-3 py-1 rounded">Ver todas</button>
                </div>
              </div>
              <div>
                <Carousel items={novelas.slice(-10).map((item) => (
                  <ContentCard key={item.id} content={item} onClick={() => navigate(`/content/${item.id}`)} />
                ))} showArrows={true} autoplay={true} autoplayInterval={4400} showDots={false} />
              </div>
            </div>
          )}

          {filteredContent.length === 0 && (
            <div className="text-center py-16">
              <p className="text-gray-400 text-xl">No se encontr contenido</p>
            </div>
          )}
        </div>
      </section>

      {/* Authentication and search modals are provided by the shared Header component. */}
    </div>
  );
};

export default HomePage;