import React from 'react';

const TMDB_IMAGE_BASE = "https://image.tmdb.org/t/p";

export default function ContentCard({ content, onClick }) {
  const posterUrl = content.poster_path ? `${TMDB_IMAGE_BASE}/w500${content.poster_path}` : 'https://via.placeholder.com/500x750?text=No+Image';
  const posterSrcSet = content.poster_path
    ? `${TMDB_IMAGE_BASE}/w300${content.poster_path} 300w, ${TMDB_IMAGE_BASE}/w500${content.poster_path} 500w, ${TMDB_IMAGE_BASE}/w780${content.poster_path} 780w`
    : undefined;
  const posterSizes = "(max-width: 640px) 50vw, (max-width: 1024px) 33vw, 20vw";

  const translateType = (type) => {
    if (!type) return '';
    const t = type.toLowerCase();
    if (t === 'movie' || t === 'pelicula') return 'Pelcula';
    if (t === 'tv' || t === 'serie') return 'Serie';
    if (t === 'novela') return 'Novela';
    return type;
  };

  return (
    <div onClick={onClick} className="glass rounded-xl overflow-hidden cursor-pointer card-hover group h-full flex flex-col">
      <div className="relative aspect-[2/3] overflow-hidden flex-shrink-0">
        <img
          src={posterUrl}
          srcSet={posterSrcSet}
          sizes={posterSizes}
          loading="lazy"
          decoding="async"
          alt={content.title}
          className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-500" />
        <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
      </div>
      <div className="p-3 flex-1 flex flex-col justify-between">
        <h4 className="font-semibold text-white text-[clamp(0.9rem,2.5vw,1rem)] line-clamp-2 mb-2" style={{minHeight: '2.4rem'}}>{content.title}</h4>
        <div className="flex items-center justify-between mt-2">
          <span className="text-xs text-gray-400">{translateType(content.content_type)}</span>
          {content.vote_average && (
            <span className="text-xs text-yellow-400"> {content.vote_average.toFixed(1)}</span>
          )}
        </div>
      </div>
    </div>
  );
}
