import React, { useState } from 'react';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

export default function FilterBar({
  genres = [],
  countries = [],
  years = [],
  selectedGenre,
  setSelectedGenre,
  selectedCountry,
  setSelectedCountry,
  selectedYear,
  setSelectedYear,
}) {
  // counter to force remount of Select components so their visual state
  // (placeholder) is reset when user clears filters
  const [resetCounter, setResetCounter] = useState(0);
  return (
    <div className="mb-6 flex flex-col sm:flex-row gap-2 items-center">
      <div className="w-full sm:w-64">
        <Select key={`genre-${resetCounter}`} value={selectedGenre} onValueChange={(v) => setSelectedGenre(v === '__all' ? undefined : v)}>
          <SelectTrigger className="w-full bg-slate-900/50 border-white/10 text-white">
            <SelectValue placeholder="Gnero" />
          </SelectTrigger>
          <SelectContent className="bg-slate-900 border-white/10">
            <SelectItem value="__all">Todos</SelectItem>
            {Array.from(new Set(genres)).sort().map((g) => (
              <SelectItem key={g} value={g}>{g}</SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div className="w-full sm:w-64">
        <Select key={`country-${resetCounter}`} value={selectedCountry} onValueChange={(v) => setSelectedCountry(v === '__all' ? undefined : v)}>
          <SelectTrigger className="w-full bg-slate-900/50 border-white/10 text-white">
            <SelectValue placeholder="Pas" />
          </SelectTrigger>
          <SelectContent className="bg-slate-900 border-white/10">
            <SelectItem value="__all">Todos</SelectItem>
            {Array.from(new Set(countries)).sort().map((c) => (
              <SelectItem key={c} value={c}>{c}</SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div className="w-full sm:w-40">
        <Select key={`year-${resetCounter}`} value={selectedYear} onValueChange={(v) => setSelectedYear(v === '__all' ? undefined : v)}>
          <SelectTrigger className="w-full bg-slate-900/50 border-white/10 text-white">
            <SelectValue placeholder="Ao" />
          </SelectTrigger>
          <SelectContent className="bg-slate-900 border-white/10">
            <SelectItem value="__all">Todos</SelectItem>
            {Array.from(new Set(years)).sort((a,b)=>b-a).map((y) => (
              <SelectItem key={y} value={y}>{y}</SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div className="w-full sm:w-auto">
        <button onClick={() => { setSelectedGenre(undefined); setSelectedCountry(undefined); setSelectedYear(undefined); setResetCounter((c) => c + 1); }} className="px-3 py-2 bg-white/5 rounded">Limpiar</button>
      </div>
    </div>
  );
}
